<template>
  <v-ons-page>
    <v-ons-toolbar class="primary">
      <div class="left">
        <v-ons-back-button></v-ons-back-button>
      </div>
      <div class="center white--text">스도쿠 {{ title }}</div>
    </v-ons-toolbar>

    <v-ons-tabbar swipeable position="auto"
        :index.sync="activeIndex" :visible="true">
      
    <template slot="pages">
      <easy-component></easy-component>
      <normal-component></normal-component>
      <hard-component></hard-component>
    </template>
      <v-ons-tab
        v-for="(tab, i) in tabs"
        :key="i"
        :label="tabs[i].label"
      ></v-ons-tab>
    </v-ons-tabbar>

    <!-- <v-ons-tabbar swipeable position="auto"
      :tabs="tabs"
      :visible="true"
      :index.sync="activeIndex"
    >
    </v-ons-tabbar>-->
    <!-- <v-ons-tabbar swipeable position="auto" :tabs="tabs" :visible="true" :index.sync="activeIndex"></v-ons-tabbar> -->
    <!-- <v-ons-navigator :page="tabs" swipeable  :visible="true" :index.sync="activeIndex"></v-ons-navigator> -->
  </v-ons-page>
</template>

<script>
import Easy from "@/components/level/Easy";
import Normal from "@/components/level/Normal";
import Hard from "@/components/level/hard";
export default {
  name: "game",
  components: {
    'easy-component': Easy,
    'normal-component': Normal,
    'hard-component': Hard,
  },
  data() {
    return {
      activeIndex: 0,
      tabs: [
        {
          // icon: this.md() ? null : 'ion-home',
          label: "Easy",
          page: Easy,
          props: {
            myProp: "This is a page prop!"
          },
          key: "easy"
        },
        {
          // icon: this.md() ? null : 'ion-ios-bell',
          label: "Normal",
          page: Normal,
          // badge: 7,
          key: "normal"
        },
        {
          // icon: this.md() ? null : 'ion-ios-settings',
          label: "Hard",
          page: Hard,
          key: "hard"
        }
      ]
    };
  },
  methods: {
    md() {
      return this.$ons.platform.isAndroid();
    }
  },
  computed: {
    title() {
      return this.tabs[this.activeIndex].label;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.back-button__icon {
  fill: white !important;
}
.header {
  text-align: center;
}

ons-list-title {
  text-transform: none;
}

ons-list-title:not(:first-of-type) {
  margin-top: 30px;
}

ons-card {
  text-align: center;
}

ons-list-item,
ons-card {
  cursor: pointer;
}
</style>



